html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}


.about-page{background-image:url(/love-animal-react/static/media/about-bg.11b8bfaa.png);background-attachment:fixed;background-size:cover}.about-page .padding{padding:300px}.about-page .about{background-color:rgba(255,255,255,0.692);padding:100px;color:#504f4f}.about-page .about p{line-height:2rem}

nav{background-color:rgba(43,42,42,0.658);display:flex;justify-content:space-between;align-items:center;padding:20px;position:-webkit-sticky;position:sticky;top:0px;left:0px;z-index:5}nav a{-webkit-text-decoration-line:none;text-decoration-line:none;color:white}nav .menu{display:flex}nav .menu li{display:flex;margin:0 20px}nav .menu li a{padding:15px}nav .menu li a:hover{background-color:orange}nav .menu .dropDown{position:relative}nav .menu .dropDown ul.subMenu{background-color:rgba(43,42,42,0.658);display:none;position:absolute;top:10px;left:-15px;width:100%;padding:15px}nav .menu .dropDown ul.subMenu li{width:100%;margin:20px 0}nav .menu .dropDown:hover ul.subMenu{display:block}

footer{background-color:#4e4e4e;display:flex;justify-content:space-between;align-items:center;padding:30px 100px}footer .copy-right{color:white}

.about-wrapper{display:flex;background-color:white;height:600px;border:2px solid #160d0d}.about-wrapper .about-item{flex-basis:80px;flex-grow:1;background-size:cover;transition:flex-basis 0.4s ease-out}.about-wrapper .about-item:hover{flex-basis:170px;transition:flex-basis 0.4s ease-in}.about-wrapper .about-item:nth-child(1){background-image:url(/love-animal-react/static/media/about1.347163a1.png)}.about-wrapper .about-item:nth-child(2){background-image:url(/love-animal-react/static/media/about2.054e8c24.png)}.about-wrapper .about-item:nth-child(3){background-image:url(/love-animal-react/static/media/about3.c0592e28.png)}.about-wrapper .about-item:nth-child(4){background-image:url(/love-animal-react/static/media/about4.3540e0ca.png)}

.news-page{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:auto;grid-template-areas:'header header header header' 'banner banner banner banner' 'news news news sidebar' 'footer footer footer footer'}nav{grid-area:header}.about-wrapper{grid-area:banner}.blog-sidebar{grid-area:sidebar}.news-wrapper{grid-area:news}footer{grid-area:footer}.widget{margin-bottom:40px;padding:40px}.widget .widget-title{margin-top:0;margin-bottom:20px;position:relative;padding-bottom:10px}.widget .widget-title:after{border-bottom:2px solid #ee163b;bottom:-1px;content:'';height:2px;left:0;position:absolute;width:50px}.widget ul.list li{margin:25px 0 5px;font-weight:500;position:relative;padding-bottom:5px;border-bottom:1px dashed #d3d3d3}.widget ul li.item{margin-bottom:15px;position:relative;padding-left:30px;padding-bottom:10px}.widget ul.list li a{color:#ee163b}.widget ul li.item p{font-size:1em;color:#a9a9a9;height:60px;overflow-y:hidden;line-height:1.8em}.widget ul li.item .date{line-height:2em;color:#a9a9a9}.news-wrapper{width:70%;margin:3% auto}.news-container{display:grid;grid-template-rows:auto;grid-template-columns:1fr 1fr;grid-gap:20px;gap:20px}.news-item{border:1px solid #000000;padding:10px}.news-title{font-size:1.2 rem;font-weight:600;line-height:4rem}.news-detail{line-height:1.8 rem;color:#4d4a4a}.news-footer{background-color:#88868658;margin-top:5px;padding:5px 10px;display:flex;justify-content:space-between;align-items:center}.news-date{font-size:0.8 rem;color:black;float:left;padding:15px}.more{float:right;background-color:#bd4e4e;color:white;padding:10px}

.AD1{background-image:url(/love-animal-react/static/media/AD-1.d24c0070.jpg);background-size:cover;height:400px;background-attachment:fixed;background-position:center center;position:relative}.AD1:hover .AD1-inside{height:100%;transition:height 0.5s ease-in}.AD1-inside{position:absolute;left:0px;bottom:0px;background-color:#ffffff4b;width:100%;color:white;text-align:center;line-height:2rem;height:40px;overflow:hidden;transition:height 0.5s ease-out}.AD1-inside:hover{height:100%;transition:height 0.5s ease-in}.text-big{font-size:30px}.animal-pic{width:1128px;margin:auto;padding:10px;display:flex;flex-wrap:wrap}.animal-pic .col{padding:15px}.animal-img{border:1px solid #6e6c6c;padding:10px;transition:all 0.3s}.animal-img:hover{transform:translateY(-10px);box-shadow:2px 5px 10px 5px #b9b9b9}.notice{width:80%;margin:30px auto}.notice .notice-title{font-size:1.5rem;color:black;font-weight:700}.notice p{color:#4e4d4d;font-size:1rem;line-height:1.5rem}

.partner-wrapper{margin:30px}.partner-wrapper .partner-title{width:80%;margin:auto;text-align:center}.partner-wrapper .partner-title h1{font-size:20px;color:#1e1e92;font-weight:800}.partner-wrapper .partner-title p{line-height:40px}.partner-pic{background-color:#cecdcd;display:flex;flex-flow:row wrap;width:80%;padding:80px;margin:auto;justify-content:center;border-radius:10px}.partner-pic .partner-thumb{background-color:white;border-radius:10px;padding:10px;margin:10px}.partner-pic .partner-thumb:nth-child(8){align-self:stretch}

.contact-wrapper{background-color:#2e2e2e;padding:50px;display:grid;grid-template-rows:1fr;grid-template-columns:1fr 1fr 1fr;grid-gap:20px;grid-auto-flow:row}.contact-wrapper div{padding:15px;background-color:#ffffff}.contact-wrapper .contact-map{grid-row:1;grid-column:1}.contact-wrapper .contact-msg{margin:0;grid-row:1;grid-column:2}.contact-wrapper .contact-form{grid-row:1;grid-column:3}.contact-wrapper .contact-form input,.contact-wrapper .contact-form textarea{width:100%;padding:25px 0px;font-size:16px;border:0;border-bottom:1px solid #d8d8d8;background:none}

